<template>

<v-card class="d-flex justify-center align-center"

>
  

     <v-col cols="10" lg="8" class = "mix">
     
       <div class="text-center">
         <v-avatar size="100" color="indigo lighten-4">
           <v-icon size="40" color="indigo">
               mdi-account
           </v-icon>
         </v-avatar>
         <h2 class="text-indigo">Create Post</h2>
         <v-form @submit.prevent ="submitHandler" ref="form" v-model="valid" >
           <v-card-text >
            <v-text-field
            v-model="post.title"
            :rules ="textRules"

            label="Title"
            solo-inverted
          ></v-text-field>
          
         <v-textarea
      v-model="post.description"
      :rules ="textRules"
      clearable
      clear-icon="mdi-close-circle"
      label="post description"
      
        ></v-textarea>
    
    <v-card-action class="mx-auto" >
             <v-btn :loading ="isLoading" type="submit" color ="indigo" :disabled="!valid">
               <span class="text-white px-8 ">Update</span>
             </v-btn>
     </v-card-action>
    </v-card-text>
           
         </v-form>
    
       </div>
     

     </v-col>
      <v-snackbar top timeout="300" color="green" v-model="snack">
      {{success}}
   </v-snackbar>
    </v-card>
   
  
</template>

<script>
import { mapActions } from 'vuex';
import axios from 'axios';

export default{
data:()=>({
    snack:false,
    success:'',
    form:false,
    isLoading:false,
    post:[],
    rules: [
        v => !!v || 'This Field is Required'
       
      ],
   textRules:[
    v => !!v || 'This field is required'
   ] 

}),
methods:{
     ...mapActions(['updatePost']),
      async submitHandler(){
        
           let post = {
             _id:this.post._id,
             title: this.post.title,
             description : this.post.description,
             author:this.post.author,
             date:this.post.date
           };
           this.isLoading=true,
           this.updatePost(post).then(res => {
            if(res.data.success){
              this.success = res.data.msg
              this.snack=true
              this.$router.push('/')
            }
           }).catch(err=>{
            console.log(err)
           }) 
       
        },
},
 async  mounted(){
            let res = await axios.get('/api/posts/' + this.$route.params.id)
             this.post = res.data.result
           
        } 

}

</script>

<style scoped>
.d-flex{
  width: 100%;
  height: 100%;
  margin-bottom: 50px;
  
}

.text {
  width:100%
}
</style>