<template>
    <div class="main">
        <v-card
    class="mx-auto"
    max-width="444px"
  >
   
    <v-toolbar
      color="deep-purple accent-4"
      cards
      dark
      flat
    >
      <v-btn icon>
        
      </v-btn>
      <v-card-title class="text-h6 font-weight-regular">
        Sign up
      </v-card-title>
      <v-spacer></v-spacer>
   
    </v-toolbar>
    <v-form
      ref="form"
      v-model="valid"
      class="pa-4 pt-6"
      @submit.prevent ="submitHandler"
      
    >
      <v-text-field
        v-model="username"
        :rules="[rules.required]"
        filled
        color="deep-purple"
        label="UserName"
         prepend-inner-icon="mdi-account"
        type="text"
      />
      
      <v-text-field
        v-model="email"
        :rules="[rules.email]"
        filled
        color="deep-purple"
        label="Email address"
         prepend-inner-icon="mdi-email"
        type="email"
      />
      <v-text-field
        v-model="password"
        :rules="[rules.password]"
        filled
        color="deep-purple"
         prepend-inner-icon="mdi-key"
        label="Password"
        style="min-height: 96px"
        type="password"
      />

      <v-text-field
        v-model="cpassword"
        filled
        color="deep-purple"
        label="Confirm_Password"
         prepend-inner-icon="mdi-key"
        style="min-height: 96px"
        type="password"
      />
      <v-card-actions>
      <v-btn
        text
        @click="$refs.form.reset()"
      >
        Clear
      </v-btn>
      <v-spacer></v-spacer>
    
             <v-btn :loading ="loading" type="submit" color ="indigo" :disabled="!valid">
               <span class="text-indigo px-8" >Register</span>
             </v-btn> 
     
    </v-card-actions>
    </v-form>
    <v-divider></v-divider>
    
    <p class="mx-auto"><span>Already Registered?</span>  <router-link to="/login">Login</router-link>   </p>
           
  </v-card>
    <v-snackbar top timeout="300" v-model="snack">
       {{success}}
   </v-snackbar>
    </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
    name:'RegisterView',
    data: () => ({
      snack:false,
      username:'',
      email: '',
      form: false,
      Loading: false,
      password:'',
      cpassword:'',
      success:'',
      rules: {
        email: v => !!(v || '').match(/.+@.+\..+/) || 'Please enter a valid email',
        length: len => v => (v || '').length >= len || `Invalid character length, required ${len}`,
        password:  v => (v && v.length >= 6) || 'password must be 6 or more characters',
        required: v => !!v || 'This field is required',
      },

    }),
    methods:{
      ...mapActions(['register']),
      submitHandler(){
        let user = {
             username: this.username,
             email: this.email,
             password: this.password,
             confirm_password: this.cpassword
        }
        this.register(user).then(res=>{
          if(res.data.success){
             this.success = res.data.msg
              this.snack=true
            this.$router.push('/login');
          }
        }).catch(err=>{
          console.log(err);
        })
      }
    }
}
</script>