           <v-icon size="40" color="dark" >
               mdi-account
           </v-icon>
         </v-avatar>
        <v-card-title> <h1 class="text-center">welcome </h1></v-card-title>
        <v-card-text>
          <v-card-title class="headind"><h3 class="text-italic">Personal Info</h3></v-card-title>
          <v-list>
