    <span class="px-3">Hello! {{user.username}}</span> 
    <p class="my-5 px-3">
      <span>You have No post yet.</span>
      Click on Add post to create a post
    </p>
  </v-card> --> 
  <v-card v-for="post in user.post" :key="post._id " width="80%" height="50%" class="pt-5 mt-5 mx-auto" id="post" >
  <div class="row">
 
      <img :src="post.image" height="200" width="270" alt="">
