      <v-card-title>
        <router-link :to = " './post/' + post._id" v-if="isLoggedIn">
            <h2 class="headline" style="text-decoration:none">{{post.title}}</h2>
          </router-link>
      </v-card-title>
      <v-card-text >
      </v-card-text>
      <v-card-action> 
        <router-link :to=" '../update/' + post._id " class="link"><v-btn flat color="green" rounded class="ml-2">Edit<v-icon>mdi-update </v-icon></v-btn></router-link>
        <v-btn flat color="error" rounded class="ml-2" @click="deletepost(post._id)"><v-icon>mdi-delete </v-icon></v-btn>
          <v-icon>mdi-charity </v-icon>
         <v-icon>mdi-comment{{}} </v-icon>
         {{post.date}}
      </v-card-action>
    </div>
    </div>
  </v-card>
  <v-snackbar top timeout="600" v-model="requestsuccess">
    {{msg}}
  </v-snackbar>
</div>
</v-container>
</template>
<script>
import { mapGetters, mapActions} from 'vuex'
 export default {
  name:'ProfileView',
  data:()=>{
    return {
      posts:[],
      count:'',
      requestsuccess:false,
      msg:''
    }
  },
 computed:{
    ...mapGetters(['user']), 
  },
    methods:{
    ...mapActions(["getProfile"]), 
    ...mapActions(["deletePost"]),

      deletepost(post){

        let get = {
          id:post
        }

       this.deletePost(get).then(res => {
            if(res.data.success){
              this.msg = res.data.msg;
              this.requestsuccess=true
             this.$router.push('/')
            }
           })  
     }   
    },
   mounted(){
   this.getProfile()
   /* this.post.sort(
      (a,b)=>{
      if(a.date < b.date){
        return 1;
      }
      if(a.date > b.date){
        return -1;
      }
      return 0;
    }
    ) */
  
    },

 }

</script>

