<template>
 <v-main class="d-flex justify-center align-center">
  

     <v-col cols="10" lg="4" class = "mx-auto">
       <v-card class="pa-4">
       <div class="text-center">
         <v-avatar size="100" color="indigo lighten-4">
           <v-icon size="40" color="indigo">
               mdi-account
           </v-icon>
         </v-avatar>
         <h2 class="text-indigo"> Login Form</h2>
         <v-form @submit.prevent ="submitHandler" ref="form" v-model="valid">
           <v-card-text>
             <v-text-field
             v-model="email"
             :rules="emailRules"
             type="email"
             label="email"
             placeholder="email"
             prepend-inner-icon="mdi-email"
             />
              <v-text-field
              v-model="password"
              :rules="passwordRules"
             :type="passwordshow?'text':'password'"
             label="password"
             placeholder="password"
             prepend-inner-icon="mdi-key"
             :append-icon="passwordshow? 'mdi-eye':'mdi-eye-off'"
             @click:append ="passwordshow = !passwordshow"
             />
           </v-card-text>
           <v-switch
           label="Remember me" 
           color="indigo"
           ></v-switch> 
           <v-card-action class="justify-center" >
             <v-btn  type="submit" color ="indigo" >
               <span class="text-white px-8">Login</span>
             </v-btn>
             <p><span>have no an account?</span><router-link to = "/register" >Create Accounts</router-link></p>
           </v-card-action>
         </v-form>
    
       </div>
      </v-card>

     </v-col>
    </v-main>
    <v-snackbar  v-model="snackbar" timeout="300" color="green" top>
        {{success}}
    </v-snackbar>
  

</template>






<script>
import { mapActions } from 'vuex'

export default {
    name:'LoginView',
    data: () => ({
    loading:false,
    snackbar:false,
    passwordshow: false,
    password: '',
    success:'',
      passwordRules: [
        v => !!v || 'password is required',
        v => (v && v.length >= 6) || 'password must be 6 or more characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ] 
  }),
  computed:{
      
  },
  methods:{
    ...mapActions(['login']),
    submitHandler(){
      let user ={
        email: this.email,
        password: this.password
      };
      this.login(user).then( res =>{
        if(res.data.success){
           this.success = res.data.msg
              this.snackbar=true
          this.$router.push('/');
        }
      }).catch(err =>{
        console.log(err);
      })
    }

  }

}
</script>


<style scoped>

v-card{
    display: block;
    border: 5px solid darkgray;
    margin: 70px 0;
    padding: 25px;
    max-width: 444px;
    margin-left: 30%;

}
form{
    display: block;
    margin-top: 30px;
    border: none;
    border-radius: 5px;

}
form input{
    margin-top: 30px;
    width: fit-content;
}
</style>