<template>

<v-card class="d-flex justify-center align-center"

>
  

     <v-col cols="10" lg="8" class = "mix">
     
       <div class="text-center">
         <v-avatar size="100" color="indigo lighten-4">
           <v-icon size="40" color="indigo">
               mdi-account
           </v-icon>
         </v-avatar>
         <h2 class="text-indigo">Create Post</h2>
         <v-form @submit.prevent ="submitHandler" ref="form" >
           <v-card-text >
            <v-text-field
            v-model="post.title"
            :rules ="textRules"
            label="Title"
            solo-inverted
          />
          <v-text-field
            v-model="user.username"
            :rules ="textRules"
            label="author"
            solo-inverted
          />
          
         <v-textarea
            v-model="post.description"
            :rules ="textRules"
            clearable
            clear-icon="mdi-close-circle"
            label="post description"
        />
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
             <img :src="imageurl" height="150">
          </v-flex> 
        </v-layout>
        <v-btn raised class="primary" @click="PickFile">upload image</v-btn>
         <input type="file" ref="fileInput" style="display:none" accept="image/*" @change="filePick">
        <v-card-action class="mx-auto" >
                <v-btn :loading ="loading" type="submit" color ="indigo">
                  <span class="text-white px-8 ">Create</span>
                </v-btn>
        </v-card-action>
        </v-card-text>
              
         </v-form>
    
       </div>
     

     </v-col>
       <v-snackbar top timeout="600" color="green" v-model="snack">
      {{success}}
   </v-snackbar>
    </v-card>

  
</template>

<script>
import { mapActions, mapGetters} from 'vuex';


export default{
data:()=>({
    snack:false,
    success:'',
    form:true,
    isLoading:false,
    post:[],
    image:null,
    imageurl:'',
    rules: [
        v => !!v || 'This Field is Required'
       
      ],
   textRules:[
    v => !!v || 'This field is required'
   ] 

}),
computed:{
    ...mapGetters(['user']),
     
  },
methods:{
     ...mapActions(['createPost']),
     ...mapActions(['getProfile']),
     PickFile(){
      this.$refs.fileInput.click();
     },
     filePick(event){
      const files = event.target.files 
      let filename = files[0].name
      if(filename.lastIndexOf('.')<=0){
        alert('image not valid')
      }
      const fileReader = new FileReader();
      fileReader.addEventListener('load',()=>{
        this.imageurl = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
      console.log(this.image)
     },
     
    submitHandler(){
         
           let post = {
            id:this.user._id,
             title: this.post.title,
             description : this.post.description,
             author : this.user.username,
             image:this.imageurl
           };
           this.createPost(post).then(res => {
            if(res.data.success){
              this.success = res.data.msg
              this.snack=true
              this.$router.push('/profile')
            }
           }).catch(err=>{
            console.log(err)
           })
       
        },
},

created(){
      this.getProfile()
      this.name =  `${this.user.username} ` 
     
}
}

</script>

<style scoped>
.d-flex{
  width: 100%;
  height: 100%;
  margin-bottom: 50px;
  
}

.text {
  width:100%
}
</style>